@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500&display=swap");
body {
  font-family: "Noto Sans JP", sans-serif;
  margin: 0;
  padding: 0;
  height: 100vh;
  background: linear-gradient(0deg, #2273c3 0%, #fd2dad 100%);
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.wrapper .btn {
  text-decoration: none;
  border: 1px solid white;
  padding: 15px 25px;
  color: white;
  border-radius: 150px;
  transition: 0.2s ease-in;
}
.wrapper .btn:hover {
  opacity: 0.8;
}
.wrapper h1 {
  font-size: 7rem;
  color: white;
  margin: 0;
  text-shadow: 2px 2px 25px rgba(0, 0, 0, 0.733);
}
.wrapper p {
  font-size: 1.2rem;
  width: 800px;
  color: #f5faff;
  margin-bottom: 20px;
}